<template>
	<div class="content">
		<div class="swiper-container" id="top-swiper">
			<div class="swiper-wrapper">
				<div class="swiper-slide swiper-div">
					<img class="swiper-img-index" src="/static/img/share/pindou.png">
				</div>
				<div class="swiper-slide swiper-div">
					<img class="swiper-img-index" src="/static/img/share/pindou.png">
				</div>
				<div class="swiper-slide swiper-div">
					<img class="swiper-img-index" src="/static/img/share/pindou.png">
				</div>
				<div class="swiper-slide swiper-div">
					<img class="swiper-img-index" src="/static/img/share/pindou.png">
				</div>
			</div>
			<div class="swiper-pagination">

			</div>
		</div>
		<div class="fixed-right-btn" @click="send">{{rtime}}App</div>
		<div class="package">
			<div class="package-top justified">
				<div class="package-name">{{packageName}}</div>
				<div class="new-price">{{newPackagePrice}}</div>
			</div>
			<div class="package-bottom justified">
				<div class="package-desc">{{packageDesc}}</div>
				<div class="old-price"><del>{{packagePrice}}</del></div>
			</div>
		</div>
		<div class="pindou-content">
			<div class="pindou-titlt"></div>
			<div class="selected-food"></div>
			<div class="pindou-remarks">
				<div class="pindou-remarks-title"></div>
				<div class="pindou-remarks-content"></div>
			</div>
		</div>
		<div class="tips">
			<div class="tips-title"></div>
			<div class="tips-content"></div>
		</div>
		<div class="shop-info">
			<div class="shop-info-title">{{shopName}}</div>
			<div class="location-info justified">
				<div class="location justified">
					<div class="location-icon">
						<img src="/static/img/share/position.png"/>
					</div>
					<div class="address">
						<span>{{shopAddress}}</span>
						<p>{{shopAddressDetail}}</p>
					</div>
				</div>
				<div class="contact-box" v-on:click="callShop">
					<img src="/static/img/share/contact.png"/>
				</div>
			</div>
		</div>
		<p>{{rtime}}</p>
		<div class="pindouing-title">
			<span class="people-count">{{groupOnNumber}}</span>
			<span></span>
		</div>
		<div class="pindouing"></div>
		<div class="initiate-failed-box">
			<div class="initiate-failed">
				<div class="initiate-failed-title"></div>
				<div class="get-qualifications"></div>
				<div class="join-other-btn" v-on:click="joinOther"><span></span></div>
			</div>
			<div class="close-join-other" v-on:click="closeSign">
				<img src="/static/img/share/close.png" alt="" />
			</div>
		</div>
		<div class="join-pindou-box">
			<div class="join-pindou">
				<div class="join-pindou-title"></div>
				<div class="join-pindou-desc">
					<span></span>
					<span>3</span>
					<span></span>
					<span>22:33:44.0</span>
					<span></span>
				</div>
				<div class="user-joined justified">
					<div class="pindou-owner">
						<div class="avatar">
							<img src="/static/img/share/avatar.jpg"/>
						</div>
						<div class="owner">
							<img src="/static/img/share/pindou-owner.png"/>
							<div class="owner-text"></div>
						</div>
					</div>
					<div class="pindou-partner">
						<div class="avatar">
							<img src="/static/img/share/avatar.jpg"/>
						</div>
					</div>
					<div class="pindou-partner">
						<div class="avatar">
							<img src="/static/img/share/avatar.jpg"/>
						</div>
					</div>
					<div class="pindou-wait">
						<div class="avatar">
							<img src="/static/img/share/pindou-wait.png"/>
						</div>
					</div>
					<div class="pindou-wait">
						<div class="avatar">
							<img src="/static/img/share/pindou-wait.png"/>
						</div>
					</div>
				</div>
				<div class="join-pindou-btn"><span></span></div>
			</div>
			<div class="close-join-pindou" v-on:click="closePindou">
				<img src="/static/img/share/close.png" alt="" />
			</div>
		</div>
		<div class="mask1" v-on:click="closeAward"></div>
		<div class="mask2"></div>
		<div class="award">
			<div class="award-title"></div>
			<!-- <p><span class="aqiyi">15</span></p>
			<p> - </p> -->
		</div>
		<div class="initiate-box">
			<div class="initiate-left">
				<p><span class="deposit">10</span><span></span></p>
				<span class="initiate-remark"></span>
				<img class="pindou-question" src="/static/img/share/question.png" v-on:click="showAward"/>
			</div>
			<div class="initiate-btn" v-on:click="signPindou">
				<span></span>
			</div>
		</div>
	</div>
</template>

<script>
import Vue from 'vue'
import VueI18n from 'vue-i18n'
import vueCookie from 'vue-cookie'
import axios from 'axios'
import VueCountdown from '@xkeshi/vue-countdown'
import $ from 'jquery'
import Swiper from 'swiper'
export default {
	name: 'pindouDetails',
	// i18n: i18n,
	components: {
		VueCountdown
	},
	data () {
		return {
			packageName: '',
			packagePrice: '',
			packageDesc: '',
			newPackagePrice: '',
			groupSize: '',
			joinNumber: '',
			groupOnNumber: '',
			shopName: '',
			shopAddress: '',
			shopAddressDetail: '',
			shopPhoneNum: '',
			rtime: 80
		}
	},
	created: function () {
		$('body').css({'background-color': '#F4F4F4', 'font-family': 'PingFangSC-Regular', 'font-size': '16px'})
		axios({
			method: 'GET',
			url: 'http://dev-new-api.beanpop.cn/event/groupOn/1'
		}).then((response) => {
			var responseData = response.data.data
			console.log(responseData)
			this.packageName = responseData.title
			this.packagePrice = '' + responseData.price
			this.newPackagePrice = '' + responseData.discountedPrice
			this.groupSize = responseData.groupSize
			this.joinNumber = responseData.joinNumber
			this.packageDesc = this.groupSize + '' + this.joinNumber + ''
			this.groupOnNumber = responseData.groupOnNumber
			var shop = responseData.shop
			this.shopName = shop.name
			this.shopAddress = shop.addressDetail
			this.shopAddressDetail = shop.nearAddress
			this.shopPhoneNum = shop.phoneNum

			// get the seleted food
			var food = responseData.goods
			var $foodDiv = '<div class="food-details justified"><span><span class="food-name"></span><span class="food-amount"></span></span><span class="price"></span></div>'
			for (var i = 0; i < food.length; i++) {
				var foodName = '' + food[i].name
				var foodAmount = '' + food[i].number + ''
				var foodPrice = '' + food[i].price
				$('.selected-food').append($foodDiv)
				$(".selected-food .food-details:eq("+ i +") .food-name").text(foodName)
				$(".selected-food .food-details:eq("+ i +") .food-amount").text(foodAmount)
				$(".selected-food .food-details:eq("+ i +") .price").text(foodPrice)
			}

			// get the remark
			var $remark = responseData.remark
			$('.pindou-remarks-content').append($remark)

			// get the tips
			var tips = responseData.tips
			var $tipsDiv = '<div class="tips-content-title"></div><div class="tips-content-text"></div>'
			for (i = 0; i < tips.length; i++) {
				var tipsTitle = tips[i].title
				var tipsValue = tips[i].value
				$('.tips-content').append($tipsDiv)
				$(".tips-content .tips-content-title:eq("+ i +")").text(tipsTitle)
				$(".tips-content .tips-content-text:eq("+ i +")").text(tipsValue)
			}

			// get pindouimg data
			var groupOn = responseData.groupOn
			var $pingBox = '<div class="pindou-box justified"><div class="user-info"><img/><span class="user-name"></span></div><div class="pindou-right justified"><div class="pindou-info"><span></span><span class="lack-people"></span><span></span><p></p></div><div class="go-pindou" v-on:click="goToPindou"><span></span></div></div></div>'
			for (i = 0; i < groupOn.length; i++) {
				var groupOnImg = groupOn[i].image
				var groupOnUser = groupOn[i].nickname
				var needNum = groupOn[i].needNum + ''
				var timeLeft = groupOn[i].timeLeft
				var seconds = timeLeft % 60
				var minutes = Math.floor((timeLeft / 60) % 60)
				var hours = Math.floor((timeLeft / 3600) % 24)
				var days = Math.floor(timeLeft / (3600 * 24)) >= 1 ? Math.floor(timeLeft / (3600 * 24)) + '' : ''
				var leftDate = '' + days + hours + ':' + minutes + ':' + seconds
				$('.pindouing').append($pingBox)
				$(".pindouing .pindou-box:eq("+ i +") img").attr('src', groupOnImg)
				$(".pindouing .pindou-box:eq("+ i +") .user-name").text(groupOnUser)
				$(".pindouing .pindou-box:eq("+ i +") .lack-people").text(needNum)
				$(".pindouing .pindou-box:eq("+ i +") .pindou-info p").text(leftDate)
			}

			for (i = 0; i < 10; i++) {
				this.$options.methods.timer(i)
			}
			// this.$options.methods.send(this.rtime)
			// get owner arawd
			var $ownerGot = responseData.ownerGot
			$('.award').append($ownerGot)
		}).catch((ex) => {
			console.log(ex)
			// var errorResponseData = ex.response.data
			// console.log(errorResponseData)
		})
	},
	mounted () {
		new Swiper('#top-swiper', {
			autoplay: true,
			pagination: {
				el: '.swiper-pagination',
				clickable: true
			},
			// pagination: '.swiper-pagination',
			paginationClickable: true,
			centeredSlides: true,
			loop: true
		})
	},
	methods: {
		goToPindou: function () {
			var cliheight = $(window).height()
			$('.mask1, .mask2').height(cliheight + 100)
			$(".join-pindou-box, .mask2").show()
			$(".initiate-box").hide()
		},
		closePindou: function () {
			$(".join-pindou-box, .mask2").hide()
			$(".initiate-box").show()
		},
		signPindou: function () {
			var cliheight = $(window).height()
			$('.mask1, .mask2').height(cliheight + 100)
			$(".initiate-failed-box, .mask2").show()
			$(".initiate-box").hide()
		},
		closeSign: function () {
			$(".initiate-failed-box, .mask2").hide()
			$(".initiate-box").show()
		},
		joinOther: function () {
			$(".initiate-failed-box, .mask2").hide()
			$(".initiate-box").show()
			$("html,body").animate({
				scrollTop: $(".pindouing").offset().top
			}, 500)
		},
		showAward: function () {
			var cliheight = $(window).height()
			$('.mask1, .mask2').height(cliheight + 100)
			$(".award, .mask1").show()
		},
		closeAward: function () {
			$(".award, .mask1").hide()
		},
		callShop: function () {
			var tel = 'tel:' + this.shopPhoneNum
			window.location.href = tel
		},
		addZero: function (event) {
			if (event < 10) {
				event = '0' + event
			}
		},
		timer: function (event) {
			console.log(event)
			// var seconds = parseInt(event % 60)
			// var minutes = parseInt((event / 60) % 60)
			// var hours = parseInt((event / 3600) % 24)
			// var days = Math.floor(event / (3600 * 24)) >= 1 ? Math.floor(event / (3600 * 24)) + '' : ''
			// seconds = this.option.methods.addZero(seconds)
			// minutes = this.option.methods.addZero(minutes)
			// hours = this.option.methods.addZero(hours)
			// var leftDate = '' + days + hours + ':' + minutes + ':' + seconds
			// $('.fixed-right-btn').text(event)
			// let this_ = this
			// setTimeout(() => {
			// 	this_.timer
			// }, 1000)
		},
		send: function () {
			let that = this
			let rtime = that.rtime
			console.log(that.rtime)
			let interval = setInterval(() => {
				if (that.rtime >= 0) {
					that.rtime--
					console.log(event)
				}
			}, 1000)
		}
	}
}
</script>

<style scoped>
@import "swiper/dist/css/swiper.css";
li{
	list-style: none;
}
a{
	text-decoration: none;
}
a:active, a:hover{
	text-decoration: none;
}
p, li{
	margin: 0px;
	padding: 0px;
}
.package-top{
	font-family: "PingFangSC-Semibold";
	font-size: 20px;
	margin-bottom: 4px;
	line-height: 28px;
}
.package-bottom{
	line-height: 17px;
	font-size: 12px;
}
.new-price{
	color: #EE6807;
}
.old-price{
	color: #999999;
	font-size: 14px;
}

.remarks-desc:last-child{
	margin-bottom: 0px;
}

.location-info{
	align-items: center;
}
.location{
	width: 85%;
	padding-right: 10px;
	border-right: 1px solid #E6E7E8;
}
.location img{
	width: 13px;
}
.contact-box{
	margin-right: 5px;
}
.contact-box img{
	width: 24px;
}
.address{
	flex: 1;
}
.address span{
	line-height: 20px;
}
.address p{
	font-size: 12px;
	color: #999999;
	line-height: 17px;
}
.pindouing-title{
	padding: 15px;
	background: #FFFFFF;
	font-family: "PingFangSC-Semibold";
	font-size: 16px;
	line-height: 22px;
	border-bottom: 1px solid #E6E7E8;
}
.people-count{
	color: #EE6807;
	margin-right: -3px;
}
.pindouing{
	padding-bottom: 0px;
	margin-bottom: 80px;
}

.go-pindou{
	width: 75px;
	height: 30px;
	background:linear-gradient(90deg,rgba(255,222,0,1),rgba(255,230,0,1));
	box-shadow:0px 2px 4px 0px rgba(255,226,0,0.3);
	border-radius:15px;
	text-align: center;
	line-height: 30px;
}
.go-pindou span{
	font-size: 14px;
}
.initiate-failed-box, .join-pindou-box{
	font-family: "PingFangSC-Regular";
	color: #333333;
	width: 80%;
	position: fixed;
	left: 10%;
	top: 30%;
	z-index: 100;
	display: none;
}
.initiate-failed, .join-pindou{
	padding: 30px 20px;
	background: #FFFFFF;
	border-radius:4px;
	margin-bottom: 25px;
}
.initiate-failed-title, .join-pindou-title{
	font-size: 18px;
	text-align: center;
	line-height: 25px;
}
.get-qualifications, .join-pindou-desc{
	font-size: 14px;
	padding: 20px 5px;
	line-height: 20px;
}
.join-other-btn, .join-pindou-btn{
	width: 100%;
	text-align: center;
	background:linear-gradient(90deg,rgba(255,222,0,1),rgba(255,230,0,1));
	box-shadow:0px 2px 4px 0px rgba(255,226,0,0.3);
	border-radius:22px;
	padding: 10px 0px;
}
.join-other-btn span, .join-pindou-btn span{
	line-height: 25px;
}
.close-join-other, .close-join-pindou{
	text-align: center;
}
.close-join-other img, .close-join-pindou img{
	width: 36px;
}

.join-pindou-desc{
	text-align: center;
}
.join-pindou-desc span:nth-child(even){
	color: #EE6807;
}
.user-joined{
	padding-bottom: 20px;
}
.avatar img{
	width: 42px;
	border-radius: 50%;
	z-index: 5;
}
.owner{
	font-size: 8px;
	line-height: 12px;
	text-align: center;
	margin-top: -5px;
	z-index: 20;
	color: #FFFFFF;
}
.owner img{
	width: 42px;
	height: 12px;
}
.owner-text{
	margin-top: -14px;
}
.award{
	display: none;
	padding: 20px 15px;
	background: #FFFFFF;
	position: fixed;
	left: 0px;
	right: 0px;
	bottom: 63px;
	border-radius:10px 10px 0px 0px;
	color: #333333;
	z-index: 100;
}
.award-title{
	text-align: center;
	font-size: 16px;
	font-family: "PingFangSC-Semibold";
	line-height: 22px;
	margin-bottom: 4px;
}
.award p, award li{
	font-size: 14px;
	line-height: 20px;
	margin-top: 8px;
}
.aqiyi{
	color: #EE6807;
}
.initiate-box{
	position: fixed;
	left: 0px;
	bottom: 0px;
	z-index: 100;
	width: 100%;
	display: flex;
}
.initiate-left{
	flex: 2;
	background: #FFFFFF;
	padding: 10px 15px;
	box-shadow:0px 1px 0px 0px rgba(221,221,221,0.5);
}
.initiate-left p{
	font-family: "PingFangSC-Semibold";
	font-size: 18px;
	color: #999999;
	line-height: 25px;
}
.deposit{
	color: #EE6807;
}
.initiate-remark{
	font-size: 12px;
	color: #666666;
	line-height: 17px;
}
.initiate-left img{
	width: 12px;
}
.initiate-btn{
	flex: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	background:linear-gradient(90deg,rgba(255,222,0,1),rgba(255,230,0,1));
}
</style>



